import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { PortalArticleComponent } from './article.component';

@NgModule({
    imports: [
        RouterModule.forChild([
            {
                path: '',
                component: PortalArticleComponent,
                children: [
                    {
                        path: '',
                        redirectTo: 'list-article',
                        pathMatch: 'full'
                    },
                    {
                        path: 'create-article',
                        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\create-article\\create-article.module')['PortalCreateArticleModule']);  });}),
                    },
                    {
                        path: 'list-article',
                        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\list-article\\list-article.module')['PortalListArticleModule']);  });}),
                    },
                    {
                        path: 'list-draft-article',
                        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\list-draft-article\\list-draft-article.module')['PortalListDraftArticleModule']);  });}),
                    }
                ]
            },
        ])
    ],
    exports: [RouterModule],
    declarations: [
        PortalArticleComponent
    ]
})

export class PortalArticleModule { }   


